

.modal {
    position: fixed;
    z-index: 5;
    top: 10px;
    left: 50%;
    transform: translate(-50%, 35px);
    background: rgba(0, 0, 0, .3);
    backdrop-filter: blur(15px);
    width: 500px;
    padding: 20px;
    opacity: 1;
    animation: modal .7s;
    border-radius: 20px;
    overflow: hidden;
    transition: all .7s;
    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__off {
        transform: translate(-50%, -10px);
        opacity: 0;
    }
}

@keyframes modal {
    0% {
        transform: translate(-50%,0);
        opacity: 0;
    }
    100% {
        transform: translate(-50%,35px);
        opacity: 1;
    }
}

@media (max-width: 600px) {
    .modal {
        width: 90%;
        p {
            font-size: 15px;
        }
    }
}