nav {
    background: rgba(0, 0, 0, .3);
    backdrop-filter: blur(15px);
    width: max-content;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    bottom: 2rem;
    transform: translateX(-50%);
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    
    a {
        background: transparent;
        padding: 0.8rem;
        border-radius: 50%;
        display: flex;
        color: var(--color-light);
        font-size: 1.1rem;
        &:hover {
            background: rgba(0, 0, 0, .3);
        }
        &.active {
            background: var(--color-primary);
            color: var(--color-bg);
            
        }
    }
}