.footer {
    background: var(--color-primary);
    padding: 3rem 0;
    text-align: center;
    font-size: .9rem;
    margin-top: 7rem;
    position: relative;
    a {
        color: var(--color-bg);   
    }
    &__logo {
        font-size: 2rem;
        font-weight: 500;
        margin-bottom: 2rem;
        display: inline-block;
    }
    &__socials {
        display: flex;
        justify-content: center;
        gap: 1rem;
        margin-bottom: 1rem;
        li {
            cursor: pointer;
            background: var(--color-bg);
            color: var(--color-white);
            padding: .8rem;
            border-radius: .7rem;
            display: flex;
            border: 1px solid transparent;
            &:hover {
                background: transparent;
                color: var(--color-bg);
                border-color: var(--color-bg);
            }
        }
    }
    &__copiright {
        margin-bottom: 4rem;
        color: var(--color-bg);
    }
    .permalinks {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        gap: 2rem;
        margin: 0 auto 3rem;
        cursor: pointer;
    }
}

// ============ MEDIA QUERIES (MEDIUM DEVICES)============
@media screen and (max-width: 1024px) {

}
// ============ MEDIA QUERIES (SMALL DEVICES)============
@media screen and (max-width: 600px) {
    .container {
        width: var(--container-width-sm);
    }
    .footer {
        .permalinks {
            flex-direction: column;
            gap: 1rem;
        }
    }

}
